import React, { Component } from 'react'
import './form.css'
export class Form extends Component {
constructor(props) {
    super(props)

    this.state = {
         username: '',
         email: '',
         number: '',
         topic : 'Payroll',
         message:''
    }
}


handelusernamechange = (event)=>{
this.setState({
    username: event.target.value
})
}

handelemailchange = (event)=>{
    this.setState({
        email: event.target.value
    })
}

handelnumberchange = (event)=>{
    this.setState({
        number: event.target.value
    })
}

handeltopicchange = (event)=>{
    this.setState({
        topic: event.target.value
    })
}

handelmessagechange = event =>{
    this.setState({
        message: event.target.value
    })
}

handlesubmit = event =>{
    alert (`${this.state.username} ${this.state.email} ${this.state.number} ${this.state.topic} ${this.state.message} `)
    event.preventDefault()
}




    render() {
        return (
<div>
<section className="form-section">
<h2 className="three-col-heading">Connect with us</h2>
<p className="three-col-para">Discover our successfully maintained values and client focus</p>
<div className="form-flex">
<div className="form-one">
<img src="./images/from.jpg" alt="form image" />
</div>

<div className="form-two">
    <form onSubmit={this.handlesubmit}>
        {/* name */}
        <label className='form-label' htmlFor="Name">Name*</label> <br />
        <input className='form-input' type="text" value={this.state.username}  onChange={this.handelusernamechange} />

{/* email and number */}
        <div className="form-two-flex">

            <div className="form-label-one">
            <label className='form-label' htmlFor="Email">Email*</label> <br />
            <input className='form-input' type="text" value={this.state.email}  onChange={this.handelemailchange} />
            </div>

            <div className="form-label-one">
            <label className='form-label' htmlFor="Phone">Phone Number*</label> <br />
            <input className='form-input' type="text" value={this.state.number}  onChange={this.handelnumberchange} />
            </div>
 </div>

 {/* services */}

<div>
<label className='form-label'>Service you need assistance for*</label> <br />

<select  className='form-input' value={this.state.topic} onChange={this.handeltopicchange} >
<option value="Payroll">Payroll</option>
<option value="PEO Services">PEO Services</option>
<option value="Managed HRMS">Managed HRMS</option>
<option value="Statutory Compliance">Statutory Compliance</option>
<option value="Talent Acquisition">Talent Acquisition</option>
<option value="CFO on Hire">CFO on Hire</option>
<option value="Corporate Services">Corporate Services</option>
</select>

</div>
{/* services end */}

{/* msg start */}
<div>
<label className='form-label' htmlFor="Message">Message*</label> <br />
<textarea className='form-input' value={this.state.message}  onChange={this.handelmessagechange}  ></textarea>
</div>
{/* msg end*/}

<button type='submit' className='form-btn'>Contact us</button>
    </form>
</div>

</div>
</section>
</div>
        )
    }
}

export default Form



